module.exports = function(owner) {
    require('./location.js')(owner);
    require('./storage.js')(owner);

    //
    owner.IsNullOrEmpty = function (val) {
        if (val != undefined && val != null && val != "") {
            return false;
        }
        return true;
    }

    owner.inAttr = function (val) {
    	if (val === true || val === 'true' || val === '') {
    		return true;
    	} else {
    		return false;
    	}
    }

    //
    owner.enpty_obj = function (obj) {
    	Object.keys(obj).map(function (key) {
    		obj[key] = null;
    	})
    	return obj;
    }

    //
    owner.IsNumber = function (value) {
    	if (isNaN(value)) {
    		return 0;
    	}
    	return value;
    }

    //
    owner.clone = function (obj) {
    	// Handle the 3 simple types, and null or undefined
    	if (null == obj || "object" != typeof obj) return obj;

    	// Handle Date
    	if (obj instanceof Date) {
    		var copy = new Date();
    		copy.setTime(obj.getTime());
    		return copy;
    	}

    	// Handle Array
    	if (obj instanceof Array) {
    		var copy = [];
    		for (var i = 0, len = obj.length; i < len; ++i) {
    			copy[i] = clone(obj[i]);
    		}
    		return copy;
    	}

    	// Handle Object
    	if (obj instanceof Object) {
    		var copy = {};
    		for (var attr in obj) {
    			if (obj.hasOwnProperty(attr)) copy[attr] = clone(obj[attr]);
    		}
    		return copy;
    	}

    	throw new Error("Unable to copy obj! Its type isn't supported.");
    }


    /*
    
    targetArr
    parentKeyWord
    selfKeyWord
    */
    owner.arrBuildTree = function (targetArr, parentKeyWord, selfKeyWord) {
    	var arr_tree = [];

    	targetArr.forEach(function (item) {
    		item.children = [];
    	})
    	targetArr.forEach(function (item) {
    		if (!(item[parentKeyWord] === 0 || item[parentKeyWord] === '0')) {
    			var that = item;
    			targetArr.forEach(function (item) {
    				if (item[selfKeyWord] == that[parentKeyWord]) {
    					item.children.push(that);
    				}
    			})
    		} else {
    			arr_tree.push(item);
    		}
    	});
    	/*children*/
    	targetArr.forEach(function (item) {
    		if (item.children.length == 0) {
    			delete item.children
    		}
    	})
    	return arr_tree;
    }

    /**
     * 
     * @param  {object|array} targetTree 
     * @param  {string} childKey   key
     * @return {object}            arraydepth
     */
    owner.treeBreakArr = function (targetTree, childKey) {
    	var arr = [],
    		deep = 0,
    		childKey = childKey || 'children';

    	function deepLoop(tree, level) {
    		var depth = level + 1;
    		deep = (deep > level) ? deep : level;
    		tree.forEach(function (item) {
    			arr.push(item);
    			if (Array.isArray(item[childKey]) && item[childKey].length > 0) {
    				deepLoop(item[childKey], depth);
    			}
    		})
    	};

    	if (typeof targetTree == 'object' && Array.isArray(targetTree)) {
    		deepLoop(targetTree, 1);
    	} else if (typeof targetTree == 'object' && !Array.isArray(targetTree)) {
    		var a = [];
    		a.push(targetTree);
    		deepLoop(a, 1);
    	};

    	return {
    		array: arr,
    		depth: deep
    	};
    }

    /**
     * refid
     * @param  {obj} ref vueref
     * @return {string}     id
     */
    owner.setRandomId = function (ref) {
    	var target = this.$refs[ref],
    		random = (Math.random() * 100000 + '').slice(0, 5);

    	var randomId = ref + '-' + random;
    	target.setAttribute('id', randomId);

    	return randomId;
    }

    /**/
    owner.sortorder = function (str) {
    	try {
    		if (str.indexOf('asc') > -1) {
    			return 'asc'
    		} else if (str.indexOf('desc') > -1) {
    			return 'desc'
    		}
    	} catch (e) {
    		return str
    	}
    }

    /**
     * yyyy-MM-dd hh:mm:ssyyyy-MM-dd
     * @param  {string} time 
     * @return {string}      
     */
    owner.timeToDate = function (time) {
    	if (/ /.test(time)) {
    		return time.split(' ')[0];
    	} else {
    		return time
    	}
    }

    /**
     * 
     * @param  {Number} length 
     * @return {string}        
     */
    owner.getRandom = function (length) {
    	var random = Math.random() + '';
    	return random.slice(2, length + 2);
    }

    /**
     * 
     * @return {string} 
     */
    owner.getTimeStrmp = function () {
    	return (new Date()).getTime();
    }

    /**
     * 
     * @param  {Number} num    
     * @param  {number} length 
     * @param  {boolean} range 0-100%
     * @return {string}        
     */
    owner.floatToPercent = function (num, length, range) {
    	var calc = (Number(num) * 100);
    	if (calc > 100 && !!range) calc = 100;
    	return calc.toFixed(length) + '%';
    }

    owner.wxPay = function (obj, callback, errcallback) {
    	//
    	WeixinJSBridge.invoke('getBrandWCPayRequest', {
    		"appId": obj.appId,
    		"timeStamp": obj.timeStamp,
    		"nonceStr": obj.nonceStr,
    		"package": obj.package,
    		"signType": "MD5",
    		"paySign": obj.paySign
    	}, function (res) {
    		//
    		if (res.err_msg == "get_brand_wcpay_request:ok") {
    			alert("");
    			!!callback && callback();
    		} else if (res.err_msg == "get_brand_wcpay_request:fail") {
    			alert("");
    			!!errcallback && errcallback();
    		} else if (res.err_msg == "get_brand_wcpay_request:cancel") {
    			alert("");
    			!!errcallback && errcallback();
    		} else {
    			alert(res.err_msg);
    			!!errcallback && errcallback();
    		};
    	});
    }

    /**
     * 
     * @param  {string} path 
     * @return {Boolean}      
     */
    owner.downloader = function (path) {
    	var eleA = document.createElement('a');
    	if ('download' in eleA) {
    		eleA.setAttribute('download', path);
    		eleA.setAttribute('href', path);

    		eleA.innerHTML = 'downloading';

    		document.body.appendChild(eleA);

    		setTimeout(function () {
    			eleA.click();
    			document.body.removeChild(eleA);
    		}, 1000 / 24);
    		return true;
    	};

    	try {
    		var elemIF = document.createElement("iframe");
    		elemIF.style.display = "none";
    		document.body.appendChild(elemIF);
    		elemIF.src = path;
    		setTimeout(function () {
    			document.body.removeChild(elemIF);
    		}, 333);
    		return true;
    	} catch (e) {
    		var form = document.createElement('form');
    		form.setAttribute('method', 'get');
    		form.setAttribute('action', path);
    		document.body.appendChild(form);
    		setTimeout(function () {
    			form.submit();
    			document.body.removeChild(form);
    		}, 1000 / 24);
    		return true;
    	}

    	if (!window.open(url)) { // popup blocked, offer direct download:
    		if (confirm("-")) {
    			location.href = url;
    		}
    	}
    	return true;
    }

    /**
     * base64
     * @param  {string}   url          
     * @param  {Function} callback     
     * @param  {string}   outputFormat 
     * @return {null}                
     */
    owner.imgToBase64 = function (url, callback, outputFormat) {
    	var canvas = document.createElement('CANVAS'),
    		ctx = canvas.getContext('2d'),
    		img = new Image;
    	img.crossOrigin = 'Anonymous';
    	img.onload = function () {
    		canvas.height = img.height;
    		canvas.width = img.width;
    		ctx.drawImage(img, 0, 0);
    		var dataURL = canvas.toDataURL(outputFormat || 'image/png');
    		callback.call(this, dataURL);
    		canvas = null;
    	};
    	img.src = url;
    }

    /**
     * 
     * @param  {Function} subType   
     * @param  {function} superType 
     * @return {null}           
     */
    owner.inheritPrototype = function (subType, superType) {
    	var protoType = Object.create(superType.prototype);
    	// Object.crear = function(superType) {
    	// 		var F = function() {};
    	// 		F.prototype = superType;
    	// 		return new F();
    	// }
    	protoType.constructor = subType;
    	subType.prototype = protoType;
    }

    /**
     * js
     * @param  {string} src js
     * @return {Boolean}     
     */
    owner.loadScript = function (src) {
    	if (!src) {
    		throw new Error('js');
    		return false;
    	};
    	var s = document.createElement('script');
    	s.type = 'text/javascript';
    	s.async = true;
    	s.src = src;
    	var x = document.getElementsByTagName('script')[0];
    	x.parentNode.insertBefore(s, x);
    	return true;
    }

    /**
     * 
     * @param  {number|string} n 
     * @return {string}     
     */
    owner.Arabia_to_Chinese = function (n) {
        var fraction = ['', ''];
        var digit = ['', '', '', '', '', '', '', '', '', ''];
        var unit = [ ['', '', ''], ['', '', '', '']  ];
        var head = n < 0 ? '': '';
        n = Math.abs(n);

        var s = '';

        for (var i = 0; i < fraction.length; i++)
        {
            var toInt = Math.floor(n * 1000) + '';
            s += (digit[Number(toInt.slice(-(3 - i), -(2 - i)))] + fraction[i]).replace(/./, '');

            // s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '');
        }
        s = s || '';
        n = Math.floor(n);

        for (var i = 0; i < unit[0].length && n > 0; i++)
        {
            var p = '';
            for (var j = 0; j < unit[1].length && n > 0; j++)
            {
                p = digit[n % 10] + unit[1][j] + p;
                n = Math.floor(n / 10);
            }
            s = p.replace(/(.)*$/, '').replace(/^$/, '')  + unit[0][i] + s;
        }
        return head + s.replace(/(.)*/, '').replace(/(.)+/g, '').replace(/^$/, '');
    }

    /**
     * objtarget
     * @param  {Object} obj    
     * @param  {Object} target 
     * @param  {Boolean} state  
     * @return {object}        
     */
    owner.mixin = function (obj, target, state) {
    	Object.keys(obj).forEach(function (key) {
    		if (state) {
    			target[key] = obj[key];
    		} else {
    			if (!target[key])
    				target[key] = obj[key];
    		}
    	});
    	return target;
    }

    /**
    * 
    * @param  {All} obj 
    * @return {String}     
    */
    owner.getType = function(obj) {
        return Object.prototype.toString.call(obj).slice(8, -1).toLowerCase();
    }
}
