/**
 * 
 * @constructor
 */
function Chain() {
	this.chain_arr = [];
}

Chain.prototype = {
	/**
	 * 
	 * @param  {function} fun 
	 * @return {this}     
	 */
	link: function (fun) {
		var that = this;
		if (typeof (fun) == 'function') {
			this.chain_arr.push(fun);
		};
		return this;
	},
	/**
	 * 
	 * @param  {Object} obj 
	 * @return {null}     [description]
	 */
	run: function (obj) {
		var that = this,
			index = 0,
			obj = obj;

		var loop = function () {
			var this_node = that.chain_arr[index];
			index++;
			if (!!this_node) {
				return this_node(obj, loop)
			}
		};

		loop();
	}
};

module.exports = Chain
